* {
  box-sizing: border-box;
  font-family: 'Rubik';
}
:root {
  --borderColor: #2D333A;
}
/* .mobile {
  display: none;
} */
/* Homepage Styles */
.home {
  display: flex;
  /* flex-direction: column; */
  justify-content: space-between;
  align-items: center;
  height: 100vh;
  max-width: 1045px;
  margin: auto;
}
.home-text {
  width: 545px;
}
.home-text h1 {
  font-size: 44px;
  color: #FFFFFF;
  margin-top: -10px;
}
.home-text p {
  font-size: 19px;
  line-height: 158.47%;
  margin-top: -10px;
}
.home-form {
  background: #3C444D;
  box-shadow: 0px 30px 40px rgba(0, 0, 0, 0.1);
  border-radius: 6px;
  padding: 30px;
}
.home-form input {
  width: 250px;
  height: 45px;
  background-color: #2D333A;
  border-radius: 6px;
  border: 0;
  padding-left: 20px;
  color: #CBCFD4;
  font-family: Rubik;
  margin: 7px 0 21px;
  font-size: 14px;
}
.home-form button,
.home-text button {
  width: 250px;
  height: 40px;
  background: linear-gradient(90deg, #F27A54 0%, #A154F2 100%);
  border-radius: 6px;
  border: 0;
  margin-top: 17px;
  text-transform: uppercase;
  font-size: 12px;
  font-family: 'Rubik';
  color: #FFFFFF;
  font-weight: bold;
  cursor: pointer;
}
.home-form label {
  font-size: 14px;
}
.home-form .error {
  font-size: 12px;
  color: red;
  margin-top: -10px;
  margin-bottom: -10px;
  box-sizing: border-box;
}
.home-form p {
  text-align: center;
  font-size: 24px;
  line-height: 28px;
  color: #FFFFFF;
  margin: 5px 0 14px;
}
.home footer {
  position: fixed;
  bottom: 30px;
  font-size: 12px;
}

/* Navbar */
.nav-links {display: none;}
.navbar {
  height: 80px;
  background-color: #3C444D;
  width: 100vw;
  position: fixed;
}
.navbar .contents {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1045px;
  margin: auto;
  text-transform: uppercase;
}
.navbar .brand {
  display: inline-flex;
}
.navbar .brand h2 {
  font-size: 20px;
  margin-left: 15px;
  font-weight: 900;
  color: #fff;
}
/* .navbar .navs {
  display: flex;
  align-items: center;
} */
.navbar a {
  text-decoration: none;
  color: #CBCFD4;
  margin-right: 30px;
  font-size: 14px;
}
.navbar a:hover{
  color: #fff;
}
.navbar a:active {
  color: #fff;
  text-decoration: underline;
}
.navbar .user {
  background-color: #2D333A;
  height: 45px;
  width: 45px;
  border-radius: 30px;
  text-align: center;
  font-weight: bold;
  font-size: 14px;
  color: #FFFFFF;
  border: 0;
}


/* Inner Layout */
.page-container {
  max-width: 1045px;
  margin: auto;
  display: flex;
  height: 50vh;
  padding-top: 100px;
}
.sidebar {
  display: flex;
  flex-direction: column;
  width: 330px;
  /* flex-grow: 1; */
  /* height: 100vh; 
  position: fixed;
  overflow-y: scroll; */
}
.sidebar p {
  font-size: 14px;
  text-align: center;
}
.main-feed {
  display: flex;
  flex-direction: column;
  width: 685px;
  margin-left: 30px;
  /* height: 100vh; 
  overflow-y: scroll; */
}
/* .main-feed :last-child() {
  margin-bottom: 50px;
} */

/* Sidebar */
.small-profile {
  width: 100%;
  background: #3C444D;
  box-shadow: 0px 30px 40px rgba(0, 0, 0, 0.1);
  border-radius: 6px;
  text-align: center;
}
.small-profile img {
  height: 128px;
  margin-top: 50px;
}
.small-profile p {
  font-size: 10px;
  text-transform: uppercase;
  color: #F27A54;
  font-weight: bold;
  margin-top: 40px;
}
.small-profile h3 {
  font-size: 24px;
  line-height: 28px;
  color: #FFFFFF;
  margin-top: 7px;
}
.small-profile div {
  font-size: 17px;
  font-style: italic;
  margin-top: -9px;
  padding-bottom: 30px;
}

/* Sidebar Tags */
.sidebar-tags {
  width: 100%;
  background: #3C444D;
  box-shadow: 0px 30px 40px rgba(0, 0, 0, 0.1);
  border-radius: 6px;
  margin-top: 20px;
  padding: 30px;
}
.sidebar-tags .link {
  text-decoration: none;
  color: #CBCFD4;
  text-transform: capitalize;
  display: block;
}
.sidebar-tags h3 {
  font-size: 24px;
  line-height: 28px;
  color: #FFFFFF;
  margin: 0 0 20px;
}

/* General Feeds Styling */
.each-feed {
  width: 100%;
  background: #3C444D;
  box-shadow: 0px 30px 40px rgba(0, 0, 0, 0.1);
  border-radius: 6px;
  margin-bottom: 20px;
  padding: 30px;
  display: flex;
  flex-direction: column;
}
.each-feed .headers {
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 0 20px;
}
.each-feed .headers .left {
  display: inline-flex;
  align-items: center;
}
.each-feed .user {
  background-color: #2D333A;
  height: 45px;
  width: 45px;
  border-radius: 30px;
  text-align: center;
  font-weight: bold;
  font-size: 14px;
  color: #FFFFFF;
  border: 0;
}
.each-feed h2 {
  font-size: 24px;
  line-height: 28px;
  color: #FFFFFF;
  margin: 0 0 0 20px;
}
.each-feed p {
  font-size: 14px;
  line-height: 22px;
  margin-top: 0;
}
.each-feed .btn {
  background-color: #2D333A;
  height: 30px;
  padding-left: 20px;
  padding-right: 20px;
  border-radius: 20px;
  text-align: center;
  font-weight: bold;
  font-size: 10px;
  color: #FFFFFF;
  border: 0;
  text-transform: uppercase;
}
.each-feed .footer .btn {
  margin-right: 20px;
}
.each-feed .headers .btn {
  text-transform: none;
}
.each-feed .image {
  border-radius: 6px;
  margin-bottom: 20px;
  height: 330px;
  object-fit: cover;
}
.each-feed .comment {
  background: #2D333A;
  border-radius: 6px;
  border: 0;
  margin-bottom: 20px;
  padding: 15px 20px;
  font-size: 14px;
  color: #CBCFD4;
}
.each-comment {
  display: inline-flex;
  margin-bottom: 20px;
}
.each-comment :last-child() {
  margin-bottom: -20px;
}
.each-comment p {
  margin-left: 20px;
  padding: 15px 20px;
  background: #2D333A;
  border-radius: 6px;
}
.each-comment .left {
  text-align: center;
  font-size: 12px;
}
.each-comment .left :first-child {
  margin-bottom: 10px;
}
.each-feed h3 {
  font-weight: bold;
  font-size: 12px;
  line-height: 22px;
  color: #FFFFFF;
  margin-top: 0;
}
/* .comment-container .btn {
  align-self: flex-end;
} */
.post .headers {
  margin-bottom: -10px;
  margin-top: -10px;
}
.post label {
  margin-bottom: 7px;
}
.post .headers .comment {
  padding: 9px 20px;
  margin-top: 10px;
  margin-left: 20px;
}
.post .headers .left {
  display: inline-flex;
  align-items: center;
}

[type="file"] {
  margin: 0 0 20px 20px;
  background: #3C444C;
}
.profile-form {
  width: 100%;
  background: #3C444D;
  box-shadow: 0px 30px 40px rgba(0, 0, 0, 0.1);
  border-radius: 6px;
  margin-bottom: 20px;
  padding: 30px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
/* .profile-form :nth-child(2) {
  margin-left: 20px;
} */
.profile-form input {
  width: 100%;
  height: 45px;
  background-color: #2D333A;
  border-radius: 6px;
  border: 0;
  padding-left: 20px;
  color: #CBCFD4;
  font-family: Rubik;
  margin: 7px 0 20px;
  font-size: 14px;
}
.profile-form label {
  font-size: 14px;
  width: 48%;
}
.profile-form button {
  width: 100%;
  height: 45px;
  background: linear-gradient(90deg, #F27A54 0%, #A154F2 100%);
  border-radius: 6px;
  border: 0;
  margin-top: 7px;
  text-transform: uppercase;
  font-size: 12px;
  font-family: 'Rubik';
  color: #FFFFFF;
  font-weight: bold;
}
.home-form label {
  font-size: 14px;
}

/* Media Queries */
@media (max-width: 1040px) {
  .navbar .contents, .home {
    padding: 0 30px;
  }
  .home h1 {
    font-size: 36px;
  }
  .home-text p {
    font-size: 16px;
  }
  .page-container {
    padding: 100px 30px 0;
  }
}

@media (max-width: 750px) {
  /* .desktop {
    display: none;
  } */
  .home {
    flex-direction: column;
    height: auto;
  }
  .home-text {
    width: auto;
    margin-top: 5vh;
    text-align: center;
  }
  .home-text p {
    display: none;
  }
  .home-form {
    margin-top: 5vh;
  }
  .sidebar {
    display: none;
  }
  .main-feed {
    margin-left: 0;
    width: 100vw;
  }
  .profile-form label {
    width: 100%;
  }
  /* .navbar .contents, {
    padding: 0 30px;
  }
  .page-container {
    padding: 100px 30px 0;
  } */
}